.message {
	background-color: #212121;
	height: rem(147);

	@include min1200 {
		position: relative;
		height: auto;
	}

	.container {
		display: flex;
		justify-content: space-between;

		@include min1200 {
			justify-content: center;
		}

		@include min540 {
			justify-content: flex-start;
		}
	}

	&__left {

		@include min1200 {
			position: absolute;
			top: 0;
			left: 0;
		}

		@include min540 {
			left: auto;
			right: 0;
		}

		img {
			vertical-align: middle;
		}
	}

	&__right {
		display: flex;
		align-items: center;

		@include min1200 {
			padding-top: rem(46);
			padding-bottom: rem(63);

			flex-wrap: wrap;
			align-items: flex-start;
			flex-direction: column;
			max-width: rem(385);
		}
		
		&_info {
			margin-right: rem(56);
			display: flex;
			flex-direction: column;
			align-items: flex-end;

			@include min1200 {
				margin-right: 0;
				max-width: rem(400);
				align-items: flex-start;

				margin-bottom: rem(14);
			}

			.title {
				font-size: rem(24);
				line-height: 180%;
				font-weight: bold;
				color: white;
			}

			.text {
				line-height: 180%;
				color: white;

				@include min540 {
					max-width: rem(223);
				}
			}
		}

		&_form {
			display: flex;
			align-items: center;

			@include min1200 {
				flex-direction: column;
				align-items: flex-start;
			}

			.field {
				height: rem(56);
				width: rem(300);
				background-color: white;
				padding: 0 rem(20);
				display: flex;
				align-items: center;

				font-size: rem(16);
				color: black;

				margin-right: rem(16);

				@include placeholder {
					color: black;
				}

				@include min1200 {
					margin-bottom: rem(12);
				}
			}

			.button {
				height: rem(56);
				background-color: #FFB571;

				display: flex;
				align-items: center;
				padding: 0 rem(28);

				&:hover {
					background-color: #EAA05C;
				}
				
				span {
					color: white;
					text-transform: lowercase;
				}
			}
		}
	}
}