.breadcrumbs {
	margin-bottom: rem(60);

	@include min1050 {
		margin-bottom: rem(48);
	}

	.container {
		display: flex;
		align-items: center;
	}

	a {
		margin-right: rem(28);

		position: relative;

		&:not(:first-child) {
			
			span {
				white-space: nowrap;
				overflow: hidden;
				text-overflow: ellipsis;
				font-size: rem(12);
			}
		}


		&::before {
			content: "";

			position: absolute;
			top: rem(7);
			right: rem(-20);
			background: url(../img/arrow_left.svg) no-repeat;
			width: rem(8);
			height: rem(7);
		}
	}

	span {
		font-size: rem(12);
		font-weight: 600;
		line-height: 160%;
		color: #4E4E4E;

		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}
}