.article_item {
	display: flex;
	flex-direction: column;


	margin-bottom: rem(40);

	&:last-of-type {
		margin-bottom: rem(60);
	}

	&__image {
		position: relative;
		height: rem(396);
		background-color: #6F6F6F;
		margin-bottom: rem(15);

		display: flex;
		align-items: center;
		justify-content: center;

		transition: var(--transition);

		@include min540 {
			height: rem(200);
		}

		* {
			transition: var(--transition);
		}

		&::before {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: rgba(28,28,28,.4);

			opacity: 0;

			transition: var(--transition);
		}

		&:hover {
			background-color: #FFB571;

			&::before {
				opacity: 1;
			}

			a {
				opacity: 1;
			}
		}

		img {
			position: relative;
			z-index: 1;

			@include min540 {
				max-width: 65%;
			}
		}

		a {
			position: absolute;
			z-index: 2;
			top: 50%;
			left: 50%;
			margin-top: rem(-10);
			margin-left: rem(-30);

			font-size: rem(18);
			line-height: 160%;
			color: white;

			opacity: 0;
			
		}
	}

	&__title {
		max-width: rem(480);
		margin-bottom: rem(10);

		&:hover {
			color: var(--c-accent);
		}
	}

	&__text {
		font-size: rem(18);
		line-height: 160%;
		color: var(--c-gray-title);
		margin-bottom: rem(15);
	}

	&__date {
		font-size: rem(12);
		font-weight: 500;
		color: #949494;
	}
}

.article_aside {
	display: flex;
	flex-direction: column;

	margin-bottom: rem(30);

	@include min1050 {
		margin-bottom: 0;
		margin-right: rem(24);

		flex: 0 0 calc((100% - 24px) / 2);
		max-width: calc((100% - 24px) / 2);

		&:nth-child(2) {
			margin-right: 0;
		}

		&:nth-child(3) {
			display: none;
		}
	}

	@include min540 {

		flex: 0 0 100%;
		max-width: 100%;
		margin-right: 0;

		&:nth-child(2) {
			display: none;
		}
	}

	&:hover {
		.article_aside__title {
			color: var(--c-accent);
		}
	}

	&:last-child {
		margin-bottom: 0;
	}

	img {
		height: rem(174);
		border-radius: rem(5);
		overflow: hidden;

		margin-bottom: rem(10);
	}

	&__title {
		font-size: rem(18);
		line-height: 156%;
		font-weight: 600;
		color: var(--c-gray-title);
		margin-bottom: rem(6);
	}

	&__date {
		font-size: rem(12);
		font-weight: 500;
		line-height: 156%;
		color: #949494;
	}
}

.article_full {
	display: flex;
	flex-direction: column;

	.title_36 {
		margin-bottom: rem(24);
	}

	&__image {
		height: rem(386);
		
		display: flex;
		align-items: center;
		justify-content: center;

		background-color: #FFB571;
		margin-bottom: rem(16);

		@include min540 {
			height: rem(200);

			img {
				max-width: 65%;
			}
		}
	}

	&__quote {
		background-color: #EBEBEB;
		border-radius: rem(5);
		padding: rem(24) rem(40);
		margin-bottom: rem(54);

		p {
			font-size: rem(18);
			line-height: 160%;
			color: var(--c-gray-title);

			@include min1050 {
				font-size: rem(14);
			}
		}
	}

	&__list {
		display: flex;
		flex-direction: column;

		margin-bottom: rem(48);

		span {
			font-size: rem(18);
			line-height: 156%;
			font-weight: 600;
			color: var(--c-gray-title);

			margin-bottom: rem(18);
		}

		b {
			font-size: rem(16);
			line-height: 160%;
			color: var(--c-gray-title);
		}

		ul {
			li {
				list-style: disc;
				margin-left: rem(20);

				font-size: rem(16);
				line-height: 160%;
				color: var(--c-gray-title);
				
				a {
					font-size: rem(18);
					line-height: 160%;
					color: var(--c-gray-title);
				}
			}

			li.active {
				color: #FFB571;
				a {
					color: #FFB571;
					text-decoration: underline;
				}
			}
		}
	}

	&__sect {
		margin-bottom: rem(48);

		.title_24 {
			margin-bottom: rem(8);
		}

		.text {
			font-size: rem(18);
			line-height: 160%;
			color: var(--c-gray-title);

			margin-bottom: rem(24);

			@include min1050 {
				font-size: rem(16);
			}

			@include min540 {
				font-size: rem(12);
			}
		}

		&_main {
			margin-bottom: rem(24);
			// width: 100%;

			.swiper-text {
				margin-top: rem(8);
				p {
					font-size: rem(12);
					font-weight: 500;
					line-height: 160%;
					color: #767676;
				}
			}

			.swiper-slide {
				img {
					width: 100%;
					height: rem(373);
					border-radius: rem(5);

					@include min540 {
						height: rem(192);
					}
				}
			}
		}

		&_thumbs {
			.swiper-slide {
				width: rem(116);
				height: rem(80);
				border-radius: rem(5);
				overflow: hidden;
				border: rem(2) solid transparent;
				opacity: .6;

				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}
			
			.swiper-slide-thumb-active {
				border: rem(2) solid #FFB571;
				opacity: 1;
			}
		}
	}

	&__text {
		font-size: rem(16);
		line-height: 160%;
		color: var(--c-gray-title);
		
		@include min540 {
			font-size: rem(12);
		}
	}

	&__quote_br {
		margin: rem(56) 0;

		font-size: rem(20);
		font-style: italic;
		line-height: 156%;
		color: var(--c-gray-title);

		padding-left: rem(66);

		border-left: rem(4) solid #FFB571;

		@include min540 {
			font-size: rem(16);
		}
	}
}


.article_related {
	margin-top: rem(92);
	// min-width: 0;

	@include min540 {
		display: none;
	}
	
	.title {
		
		font-size: rem(28);
		line-height: 156%;
		font-weight: 600;
		color: var(--c-gray-title);

		@include min1050 {
			font-size: rem(18);
		}
	}

	@include min1050 {
		.article_aside {
			.article_aside__title {
				font-size: rem(16);
			}

			img {
				height: rem(215);
			}
		}
	}

	

	&__top {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: rem(14);

		.page {
			display: flex;
			align-items: center;

			margin-right: rem(20);

			&_prev {
				margin-right: rem(10);
			}

			&_prev, &_next {
				width: rem(38);
				height: rem(38);
				border-radius: 50%;

				display: flex;
				align-items: center;
				justify-content: center;

				border: rem(1) solid var(--c-gray-title);

				&:hover {
					background-color: black;

					border-color: black;
					
					svg {
						use {
							stroke: white;
						}
					}
				}

				svg {
					use {
						stroke: var(--c-gray-title);
					}
				}
			}
		}
	}

	&__slider {
		// min-width: 0;
		// margin-right: -75%;

		.article_aside {
			flex: 0 0 100%;
			max-width: 100%;
			margin-right: 0;
		}
	}
}

.page-article {

	@include min1200 {
		.wrapper__right_col1.mr-120 {
			margin-right: rem(32);
			flex: 0 0 calc(72% - 32px);
			max-width: calc(72% - 32px);
		}
	}

	@include min1050 {
		.wrapper__right_col1.mr-120 {
			margin-right: 0;
			flex: 0 0 100%;
			max-width: 100%;
		}
	}

	.wrapper__right_col2 {

		

		@include min1050 {
			display: none;
		}
	}
}