.slider_price {

	margin-bottom: rem(140);

	.swiper-control {
		display: flex;
		justify-content: space-between;
		padding: rem(32) 0;

		@include min1050 {
			justify-content: flex-end;
			padding: rem(16) 0;
		}

		.swiper-page {
			@include min1050 {
				display: none;
			}
		}

		.swiper-prev,
		.swiper-next {
			width: rem(50);
			height: rem(19);

			svg {
				use {
					stroke: #3E3E3E;
				}
			}

			@include min1050 {
				width: rem(38);
				height: rem(38);
				border-radius: 50%;

				display: flex;
				align-items: center;
				justify-content: center;

				border: rem(1) solid #3E3E3E;

				&:hover, &:active {
					background-color: black;
					border-color: black;

					svg {
						use {
							stroke: white;
						}
					}
				}

				svg {
					width: rem(16);
				}
			}
		}

		.swiper-next {
			@include min1050 {
				margin-left: rem(10);
			}
		}
	}

	.item {
		display: flex;
		background-color: #141105;
		height: rem(460);

		@include min1050 {
			position: relative;
			height: rem(378);
			background-color: transparent;
		}

		@include min540 {
			height: rem(570);
		}

		&__left, &__right {
			flex: 0 0 50%;
			max-width: 100%;

			@include min1050 {
				flex: 0 0 100%;
				max-width: 100%;
			}
		}

		&__left {
			padding: rem(90) rem(124);

			display: flex;
			flex-direction: column;

			@include min1200 {
				padding: rem(90) rem(70) rem(40) rem(70);
			}

			@include min1050 {
				background: linear-gradient(199.03deg, rgba(0, 0, 0, 0.37) 0%, rgba(0, 0, 0, 0.81) 98.47%);
				padding: rem(96) rem(70) rem(53) rem(48);
			}

			.title {
				display: flex;
				align-items: center;

				font-family: var(--fontBrygada);
				font-weight: bold;
				font-size: rem(14);
				letter-spacing: rem(3.4);
				text-transform: uppercase;
				color: var(--c-accent2);

				margin-bottom: rem(20);

				@include min1050 {
					position: relative;
					font-size: rem(12);
					max-width: rem(152);
					
					margin-bottom: rem(30);
				}

				&::before {
					content: "";
					width: rem(80);
					height: rem(1.2);
					background-color: #FFB571;
					margin-right: rem(8);

					@include min1050 {
						position: absolute;
						top: rem(7);
						left: rem(-46);
						width: rem(40);
						height: rem(1.5);
					}
				}
			}

			.text {
				font-size: rem(24);
				color: white;
				font-weight: 500;
				line-height: 156%;
				margin-bottom: rem(36);

				@include min1050 {
					max-width: rem(476);
					font-size: rem(16);
					margin-bottom: rem(32);
				}

				@include min540 {
					max-width: rem(241);
				}
			}

			.button_border {
				align-self: flex-start;
			}
		}

		&__right {

			@include min1050 {
				position: absolute;
				top: 0;
				left: 0;
				height: rem(378);
				width: 100%;
				z-index: -1;
			}

			@include min540 {
				height: rem(570);
			}

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}

}