.product_item {
	position: relative;
	display: flex;
	flex-direction: column;

	&:hover {
		z-index: 3;
		&::before {
			display: block;
			// opacity: 1;
		}

		.product_item__button {
			// opacity: 1;
			display: flex;
			// pointer-events: visible;
		}
	}
	@include max1050 {
		&::before {
			content: "";
			position: absolute;
			top: rem(-20);
			left: rem(-16);
			width: calc(100% + 32px);
			height: calc(100% + 100px);
			border-radius: rem(5);
			background-color: #F8F8F8;
			box-shadow: 0px 4px 5px rgba(0, 0, 0, 0.2),
				0px 4px 50px rgba(0, 0, 0, 0.4);
			z-index: -1;
			display: none;

			transition: var(--transition);
			// pointer-events: none;
		}
	}
	
	
	&__slider {
		position: relative !important;
		margin: 0 !important;
		padding: 0 !important;

		.swiper-slide {
			height: rem(392);
		}

		.swiper-nav {
			position: absolute;
			top: 50%;
			margin-top: rem(-20);
			z-index: 2;
			width: 100%;
			display: flex;
			justify-content: space-between;

			button {
				width: rem(45);
				height: rem(60);

				display: flex;
				align-items: center;
				justify-content: center;
				background-color: rgba(255, 255, 255, .34);

				&:hover {
					background-color: rgba(255, 255, 255, .65);
				}
			}
		}
	}

	&__image {
		position: relative;
		z-index: 2;
		height: rem(392);
		overflow: hidden;
		margin-bottom: rem(28);

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__title {
		position: relative;
		z-index: 2;
		margin-bottom: rem(7);
		font-weight: 600;
		font-family: var(--fontBrygada);
		color: var(--c-accent);
		font-size: rem(20);
		letter-spacing: rem(1.2);
		text-transform: uppercase;

		@include min1050 {
			font-size: rem(16);
		}
	}

	&__subtitle {
		position: relative;
		z-index: 2;
		font-size: rem(18);
		color: #545454;
		margin-bottom: rem(33);

		@include min1050 {
			font-size: rem(14);
		}
	}

	&__list {
		position: relative;
		z-index: 2;
		padding: rem(20) 0;
		margin-bottom: rem(28);

		border-top: rem(0.5) solid var(--c-black-40);
		border-bottom: rem(0.5) solid var(--c-black-40);

		ul {
			display: flex;
			flex-direction: column;

			li {
				margin-bottom: rem(12);
				color: #6F6F6F;
				line-height: 156%;
				font-weight: 600;

				&:last-child {
					margin-bottom: 0;
				}
			}
		}
	}

	&__button {
		position: absolute;
		bottom: rem(-50);
		z-index: 2;
		left: 0;
		// display: flex;
		flex-wrap: wrap;

		display: none;
		// opacity: 0;

		@include min1050 {
			position: relative;
			display: block;
			bottom: auto;
			left: auto;
		}

		@include min1050 {
			display: flex;
			flex-wrap: nowrap;

			.button_black,
			.button_transparent {
				padding-left: 0;
				padding-right: 0;
				// flex: 1;
				flex-grow: 1;
				min-width: 0;
			}
		}
	}
}