.example_work {
	margin-bottom: rem(60);

	.container {
		position: relative;

		@include min1050 {
			display: flex;
			flex-direction: column;
		}
	}

	@include max1050 {
		.swiper {
			padding: rem(80) rem(80);
			margin: rem(-80) rem(-80);

			.swiper-slide {
				opacity: 0;
			}

			.swiper-slide-next,
			.swiper-slide-active {
				opacity: 1;
			}
		}
	}

	@include min1050 {
		.swiper {
			width: 100%;
			min-width: 0;
		}
	}

	

	.section_header {
		margin-bottom: rem(28);
		position: relative;
		z-index: 2;

		@include min1050 {
			margin-bottom: rem(14);
		}
	}

	.section-navigation {
		position: absolute;
		z-index: 2;
		top: rem(10);
		right: 0;

		@include min1050 {
			position: relative;
			top: auto;
			right: auto;
			order: 10;

			margin-top: rem(54);
			width: 100%;
			display: flex;
			justify-content: space-between;

			&__prev {
				margin-left: auto;
			}
		}
	}

}