.place_order {
	height: rem(178);
	width: 100%;

	background: url(../img/place_order_bg.jpg) no-repeat top center;
	background-size: cover;

	display: flex;
	align-items: center;

	margin-bottom: rem(168);

	@include min1200 {
		height: 100%;

		background: url(../img/place_order_bg_tablet.jpg) no-repeat top center;
		background-size: cover;

		margin-bottom: rem(92);
	}

	.container {
		width: 100%;
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-between;

		@include min1200 {
			padding: rem(58) rem(66) rem(14) rem(66);
		}

		@include min540 {
			padding: rem(62) rem(20) rem(34) rem(20);
		}
	}

	.section_header {
		.title {
			color: white;
		}

		@include min1200 {
			margin-bottom: rem(44);
		}
	}

	&__items {
		display: flex;
		align-items: center;

		@include min {
			flex-wrap: wrap;
		}
	}

	&__item {
		display: flex;
		flex-direction: column;

		margin-right: rem(36);

		@include min1440 {
			// margin-right: rem(16);
		}

		@include min {
			margin-bottom: rem(46);
		}

		@include min540 {
			flex: 0 0 calc((100% - 36px) / 2);
			max-width: calc((100% - 36px) / 2);

			&:nth-child(2n) {
				margin-right: 0;
			}
		}

		&:last-child {
			margin-right: 0;
		}

		svg {
			margin-bottom: rem(14);
		}

		b {
			color: white;
			line-height: 130%;
			font-weight: 600;

			// flex: 0 0 rem(156);
			// max-width: rem(156);
		}
	}
}