.advantages {

	margin-bottom: rem(140);

	@include min1050 {
		margin-bottom: rem(120);
	}

	.section_header {
		margin-bottom: rem(20);

		@include min1050 {
			margin-bottom: rem(14);
		}
	}

	&__items {
		display: flex;
		flex-wrap: wrap;
		padding: rem(68) 0 !important;
		border-bottom: rem(0.5) solid var(--c-black-40);
		border-top: rem(0.5) solid var(--c-black-40);

		@include min1050 {
			border-bottom: 0;
			border-top: 0;
			padding: 0 !important;
		}

		.swiper-control {
			display: none;
			// display: flex;
			justify-content: space-between;
			padding: rem(32) 0;
			width: 100%;

			@include min1050 {
				display: flex;
				justify-content: flex-end;
				padding: rem(16) 0;
			}

			.swiper-prev,
			.swiper-next {
				width: rem(50);

				svg {
					use {
						stroke: #3E3E3E;
					}
				}

				@include min1050 {
					width: rem(38);
					height: rem(38);
					border-radius: 50%;

					display: flex;
					align-items: center;
					justify-content: center;

					border: rem(1) solid #3E3E3E;

					&:hover,
					&:active {
						background-color: black;
						border-color: black;

						svg {
							use {
								stroke: white;
							}
						}
					}

					svg {
						width: rem(16);
					}
				}
			}

			.swiper-next {
				@include min1050 {
					margin-left: rem(10);
				}
			}
		}
	}

	&__item {
		// width: calc((100% - 360px) / 5);

		display: flex;
		flex-direction: column;

		// margin-right: rem(90);

		@include min1050 {
			background-color: white;
			border-radius: rem(5);
			padding: rem(20) rem(26);
			height: rem(250);
		}

		&:last-child {
			margin-right: 0;
		}

		.icon {
			margin-bottom: rem(20);
		}

		.title {
			font-size: rem(18);
			font-weight: 600;
			line-height: 130%;
			color: var(--c-gray-title);
			margin-bottom: rem(13);
		}

		.text {
			line-height: 160%;
			color: var(--c-gray-text);
		}
	}

}