.smart_menu {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: white;

	display: none;
	flex-direction: column;

	z-index: 50;

	overflow-y: auto;

	&__header {
		// width: 100%;
		padding: rem(18) 0;
		margin-left: rem(24);
		margin-right: rem(24);

		border-bottom: rem(1) solid var(--c-black-40);
		margin-bottom: rem(18);

		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;
		// width: 100%;

		.locale {
			display: flex;
			align-items: center;
			margin-left: rem(52);

			span {
				font-weight: 500;
				margin-left: rem(5);
			}
		}

		.call {
			span {
				color: #EAA05C;
			}
		}
	}

	&__wrap {
		// margin-left: rem(24);
		// margin-right: rem(24);

		&_header {
			padding-bottom: rem(20);
			margin-bottom: rem(30);
			border-bottom: rem(1) solid var(--c-black-40);

			margin-left: rem(24);
			margin-right: rem(24);

			span {
				font-size: rem(20);
				font-weight: 500;
			}
		}

		.prev {
			display: flex;
			align-items: center;

			margin-left: rem(24);
			margin-right: rem(24);

			margin-top: rem(32);
			margin-bottom: rem(50);

			span {
				margin-left: rem(7);
				font-size: rem(12);
				font-weight: 500;
			}
		}
	}

	&__wrap.hide {
		display: none;
	}

	.sub {
		display: none;
	}

	.sub.active {
		display: block;
	}

	&__search {
		padding-bottom: rem(24);
		margin-bottom: rem(40);
		border-bottom: rem(1) solid var(--c-black-40);

		margin-left: rem(24);
		margin-right: rem(24);

		.field-set {
			position: relative;

			button {
				position: absolute;
				top: 50%;
				left: rem(15);

				margin-top: rem(-10);

				width: rem(22);
				height: rem(22);

				display: flex;
				align-items: center;
				justify-content: center;
			}

			.field {
				width: 100%;
				height: rem(54);

				border-radius: rem(5);
				border: rem(1) solid #FFB571;

				padding: 0 rem(20) 0 rem(52);

				display: flex;
				align-items: center;

				color: var(--c-black-40);
				font-weight: 500;
				font-size: rem(16);

				@include placeholder {
					color: var(--c-black-40);
				}
			}
		}
	}

	&__nav {
		display: flex;
		flex-direction: column;


		.subtitle {
			margin-left: rem(24);
			margin-right: rem(24);

			ul {
				li {
					margin-bottom: rem(26);

					&:last-child {
						margin-bottom: 0;
					}

					a {
						font-weight: 500;
						color: #5C5C5C;
					}

					a.active {
						color: #212121;
					}
				}
			}
		}

		.sub-tt {
			margin-left: rem(24);
			margin-right: rem(24);

			ul {
				li {
					margin-bottom: rem(40);

					&:last-child {
						margin-bottom: 0;
					}

					a, button {
						font-size: rem(20);
						text-transform: uppercase;
						font-weight: 500;

						display: flex;
						align-items: center;

						svg {
							margin-left: rem(16);
						}
					}
				}
			}
		}

		.sub-item {
			margin-left: rem(24);
			margin-right: rem(24);

			.item {
				display: flex;
				flex-direction: column;
				padding-bottom: rem(20);
				border-bottom: rem(1) solid var(--c-black-40);
				margin-bottom: rem(20);

				img {
					height: rem(136);
					margin-bottom: rem(10);
				}
				span {
					font-size: rem(12);
					line-height: 140%;
				}
			}
		}
		

		.top {
			border-bottom: rem(1) solid var(--c-black-40);
			margin-bottom: rem(42);
			padding-bottom: rem(42);

			padding-left: rem(24);
			padding-right: rem(24);

			ul {
				li {
					margin-bottom: rem(40);

					&:last-child {
						margin-bottom: 0;
					}

					button, a {
						font-size: rem(20);
						font-weight: 500;
						text-transform: uppercase;

						display: flex;
						align-items: center;

						span {
							margin-right: rem(14);
							color: var(--c-black-60);
						}
					}
				}
			}
		}

		.bottom {
			border-bottom: rem(1) solid var(--c-black-40);
			margin-bottom: rem(42);
			padding-bottom: rem(42);
			padding-left: rem(24);
			padding-right: rem(24);

			ul {
				li {
					margin-bottom: rem(20);

					&:last-child {
						margin-bottom: 0;
					}

					button, a {
						display: flex;
						align-items: center;

						span {
							font-size: rem(16);
							font-weight: 500;
							margin-right: rem(10);
						}
					}
				}
			}
		}
	}


	&__bottom {
		padding-left: rem(24);
		padding-right: rem(24);

		display: flex;
		flex-direction: column;

		margin-top: auto;
		margin-bottom: rem(22);

		a {
			color: var(--c-black-50);
			font-weight: 600;
			margin-bottom: rem(8);

			&:last-child {
				margin-bottom: 0;
			}
		}
	}
}

.smart_menu.active {
	display: flex;
}