.reviews {
	margin-bottom: rem(134);

	@include min540 {
		margin-bottom: rem(180);
	}

	.container {
		display: flex;
		flex-direction: column;
		align-items: center;


	}

	&__header {
		display: flex;
		flex-direction: column;
		align-items: center;

		margin-bottom: rem(60);

		@include min1050 {
			width: 100%;
			margin-bottom: rem(15);
			align-items: flex-start;
		}

		.title {
			color: var(--c-gray-title);
			font-size: rem(36);
			font-weight: 600;
			line-height: 156%;
			margin-bottom: rem(4);

			@include min1050 {
				font-size: rem(18);
				margin-bottom: 0;
			}
		}

		.link {
			color: var(--c-gray-text);
			font-weight: 500;
			line-height: 156%;

			@include min1050 {
				display: none;
			}
		}
	}

	&__content {
		width: 100%;

		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;

		&_center {
			flex: 0 0 rem(850);
			max-width: rem(850);

			@include min1050 {
				flex: 0 0 100%;
				max-width: 100%;
			}

			.swiper {
				padding: rem(40);
				margin: rem(-40);

				@include min1050 {
					padding: rem(12);
					margin: rem(-12);
				}

				.swiper-page__wrap {
					margin-top: rem(87);

					@include min540 {
						display: flex;
						align-items: center;
						justify-content: flex-end;
					}

					.link {
						display: none;

						@include min540 {
							display: block;
							font-weight: 500;
							color: #4F4F4F;
							white-space: nowrap;
							margin-left: auto;
						}
					}

					@include min1050 {
						display: none;
					}

					@include min540 {
						display: flex;
						margin-top: rem(28);
					}
				}

				.swiper-page {
					

					@include min540 {
						justify-content: flex-end;
						margin-left: auto;
					}

					
				}
			}
		}

		&_nav_prev,
		&_nav_next {
			svg {
				use {
					stroke: black;
				}
			}
		}

		@include min1050 {
			&_nav_prev,
			&_nav_next {
				display: none;
			}
		}
	}
}