.info {
	margin-bottom: rem(134);

	@include min1050 {
		margin-bottom: rem(120);
	}

	@include min660 {
		margin-bottom: rem(100);
	}

	.container {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;

		// padding: 0 rem(195);
	}

	&__left {
		flex: 0 0 rem(517);
		max-width: rem(517);
		margin-right: rem(46);

		@include min1050 {
			position: relative;
			flex: 0 0 calc(50% - 36px);
			max-width: calc(50% - 36px);

			margin-right: rem(36);

			&::before {
				content: "";
				position: absolute;
				top: rem(24);
				left: rem(16);
				border: rem(1) solid #FFB571;

				width: calc(100% - 32px);
				height: calc(100% - 48px);
			}
		}

		@include min660 {
			flex: 0 0 100%;
			max-width: 100%;
			margin-right: 0;

			height: rem(315);
			margin-bottom: rem(30);
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__right {
		flex: 0 0 rem(442);
		max-width: rem(442);

		@include min1050 {
			flex: 0 0 50%;
			max-width: 50%;
		}
		@include min660 {
			flex: 0 0 100%;
			max-width: 100%;
		}

		.title {
			font-size: rem(40);
			font-family: var(--fontBrygada);
			font-weight: normal;
			font-style: italic;
			margin-bottom: rem(54);

			@include min1050 {
				font-size: rem(22);
				margin-bottom: rem(24);
			}

			@include min660 {
				font-size: rem(20);
				max-width: rem(190);
			}
		}

		.subtitle {
			display: flex;
			align-items: center;

			height: rem(100);
			padding-left: rem(26);
			margin-bottom: rem(26);

			border-left: rem(1) solid #6F6F6F;

			@include min1050 {
				padding-left: rem(20);
				height: rem(66);
			}

			@include min660 {
				letter-spacing: rem(1.5);
			}

			span {
				color: var(--c-accent2);
				font-size: rem(25);
				font-weight: normal;
				text-transform: uppercase;
				font-family: var(--fontSpartan);

				@include min1050 {
					font-size: rem(14);
				}
			}
		}

		p {
			font-weight: 300;
			line-height: 195%;
			color: #2C2C2C;

			@include min660 {
				font-size: rem(12);
			}
		}
	}
}