.footer {
	background-color: #212121;

	
	padding-bottom: rem(30);

	.container {
		position: relative;
		display: flex;
		flex-wrap: wrap;
	}

	&__tab {
		display: flex;
		flex-direction: column;
		

		

		&_header {
			padding: rem(14) 0;
			margin-bottom: rem(20);
			// display: flex;
			align-items: center;
			justify-content: space-between;
			border-bottom: rem(1) solid var(--c-white-20);
			cursor: pointer;
			display: none;

			@include min1050 {
				display: flex;
			}
			
			button {
				transform: rotate(180deg);
			}

			span {
				font-weight: bold;
				color: white;
			}
		}

		&__wrap {
			@include min1050 {
				display: none;
			}
		}
	}

	&__tab.active {

		@include min1050 {
			border-bottom: rem(1) solid var(--c-white-20);
		}

		.footer__tab_header {
			button {
				transform: rotate(0deg);
			}
		}

		.footer__tab__wrap {
			display: block;
		}
	}

	&__button_arrow {
		position: absolute;
		top: rem(100);
		right: rem(20);
		width: rem(50);
		height: rem(50);
		border-radius: 50%;

		display: flex;
		align-items: center;
		justify-content: center;
		background-color: white;

		// box-shadow: 0px 0px 10px rgba(174, 174, 174, 01), 0px 0px 3px rgba(0, 0, 0, 01);

		@include min1050 {
			display: none;
		}

		&:hover {
			transform: translateY(-10px);
		}

	}

	&__left, &__right {
		padding-top: rem(90);
		flex: 0 0 50%;
		max-width: 50%;

		@include min1050 {
			flex: 0 0 100%;
			max-width: 100%;
			padding-top: rem(26);
		}
	}

	&__left {
		padding-bottom: rem(40);
		border-right: rem(1) solid var(--c-white-20);

		display: flex;
		flex-direction: column;

		@include min1050 {
			border-right: 0;
		}

		.logo {
			margin-bottom: rem(38);

			align-self: flex-start;
		}
		.time {
			margin-bottom: rem(32);
			color: white;
			font-weight: 500;
		}

		.flex {
			margin-bottom: rem(24);

			.text {
				font-size: rem(12);
				line-height: 180%;
				font-style: italic;
				color: var(--c-white-50);

				max-width: rem(346);

				@include min1050 {
					display: none;
				}
			}

			.block_list {
				margin-right: rem(50);

				&:last-child {
					margin-right: 0;
				}
			}
		}
	}

	&__right {

		display: flex;
		flex-wrap: wrap;
		flex-direction: column;
		// align-self: flex-start;
		margin-left: rem(190);

		flex: 0 0 rem(334);
		max-width: rem(334);

		height: rem(412);

		@include min1200 {
			margin-left: rem(75);
		}

		@include min1050 {
			margin-left: 0;
			flex: 0 0 100%;
			max-width: 100%;
			height: 100%;
		}

		&_list {
			display: flex;
			flex-direction: column;
			margin-right: rem(90);

			.text {
				display: none;

				@include min1050 {
					display: block;
					margin-top: rem(68);
					font-size: rem(12);
					color: var(--c-white-50);
					font-style: italic;
				}
			}

			@include min1050 {
				margin-right: 0;
				flex: 0 0 100%;
				max-width: 100%;

				// flex-wrap: wrap;
				// flex-direction: row;
				// align-items: center;
				// justify-content: space-between;

				&:nth-child(2) {
					order: 10;

					margin-top: rem(60);

					.top {
						align-self: flex-start;
						padding-bottom: rem(2);
						border-bottom: rem(2) solid white;
					}
				}
			}

			&_tab {
				margin-bottom: rem(20);

				@include min1050 {
					display: flex;
					align-items: center;
					justify-content: space-between;
					padding-bottom: rem(14);
					border-bottom: rem(1) solid var(--c-white-20);
					
				}
			}

			&:last-child {
				margin-right: 0;
			}

			&:first-child {
				margin-bottom: rem(77);

				@include min1050 {
					margin-bottom: rem(60);
				}
			}
			
			.arrow {
				display: none;

				@include min1050 {
					display: block;
					transform: rotate(180deg);
				}
			}

			.top {
				font-size: rem(16);
				line-height: 120%;
				font-weight: bold;
				color: white;
				margin-bottom: rem(28);

				@include min1050 {
					margin-bottom: 0;
					font-size: rem(14);
				}

				&:hover {
					color: var(--c-accent);
				}
			}

			ul {

				@include min1050 {
					display: none;
				}

				li {
					margin-bottom: rem(14);

					&:last-child {
						margin-bottom: 0;
					}

					a {
						
						display: block;
						font-weight: 500;
						line-height: rem(17);
						color: var(--c-white-60);

						&:hover {
							color: var(--c-accent);
						}
					}
				}
			}
		}

		&_list.active {

			.arrow {
				@include min1050 {
					transform: rotate(0deg);
				}
			}

			ul {
				@include min1050 {
					display: block;
				}
			}
		}

	}


	&__bottom {
		border-top: rem(1) solid var(--c-white-20);

		flex: 0 0 100%;
		max-width: 100%;

		padding-top: rem(16);
		padding-bottom: rem(34);

		display: flex;
		justify-content: space-between;

		@include min1050 {
			padding-top: rem(28);
			margin-top: rem(65);
			flex-direction: column;
		}

		.text {
			flex: 0 0 rem(393);
			max-width: rem(393);

			font-size: rem(12);
			line-height: 180%;
			color: rgba(255,255,255,.45);

			@include min1050 {
				flex: 0 0 auto;
				// max-width: auto;
			}
		}

		.link {
			flex: 0 0 rem(393);
			max-width: rem(393);

			font-size: rem(12);
			color: rgba(255, 255, 255, .45);
			text-decoration: underline;
			margin-top: rem(12);

			@include min1050 {
				flex: 0 0 auto;
				margin-top: rem(72);
			}

			&:hover {
				text-decoration: none;
			}
		}

		&_right {
			display: flex;
			align-items: center;

			@include min1050 {
				margin-top: rem(42);
			}

			a {
				font-size: rem(12);
				color: var(--c-white-50);
				

				display: flex;
				align-items: center;

				img {
					margin-left: rem(7);
				}
			}

			img {
				// height: rem(20);
			}
		}
	}
}