.actions {
	.title_36 {
		margin-bottom: rem(22);
	}

	.page_control {
		margin-top: rem(64);
	}
}

.action_item {
	height: rem(422);
	overflow: hidden;

	display: flex;
	margin-bottom: rem(46);

	background-color: white;

	border-top-right-radius: rem(5);
	border-bottom-right-radius: rem(5);

	@include min1050 {
		height: rem(219);
		margin-bottom: rem(24);
	}

	@include min660 {
		height: auto;
		flex-wrap: wrap;
	}

}

.action_item_first {
	.left {
		position: relative;
		flex: 0 0 72%;
		max-width: 72%;

		display: flex;
		align-items: center;
		justify-content: center;

		@include min1340 {
			flex: 0 0 50%;
			max-width: 50%;
		}

		@include min660 {
			flex: 0 0 100%;
			max-width: 100%;
			height: rem(220);
		}

		&::before {
			content: "";
			position: absolute;
			top: rem(-19);
			right: rem(-200);

			width: rem(442);
			height: rem(442);
			border-radius: 50%;
			background-color: #FFB571;
			z-index: 0;

			@include min1050 {
				width: rem(229);
				height: rem(229);

				top: rem(-5);
				right: rem(-86);
			}

			@include min660 {
				top: rem(145);
				right: auto;
				left: rem(-50);
			}
		}

		img {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			object-fit: cover;

			z-index: 1;
		}

		&__count {
			position: relative;
			z-index: 2;
			display: flex;
			align-items: center;
			justify-content: center;

			width: rem(126);
			height: rem(126);
			border-radius: 50%;
			background-color: black;

			font-weight: 800;;
			font-size: rem(26);
			color: white;

			@include min1050 {
				width: rem(65);
				height: rem(65);

				font-size: rem(14);
			}
		}
	}

	.right {
		position: relative;
		z-index: 2;
		flex: 0 0 28%;
		max-width: 28%;

		padding: rem(46) rem(68);

		display: flex;
		flex-direction: column;

		@include min1340 {
			flex: 0 0 50%;
			max-width: 50%;
		}

		@include min1050 {
			padding: rem(20) rem(50);
		}

		@include min660 {
			flex: 0 0 100%;
			max-width: 100%;
			padding: rem(20) rem(32);
		}

		p {
			font-size: rem(24);
			line-height: 160%;
			font-weight: 500;
			color: black;
			margin-bottom: rem(28);

			@include min1440 {
				font-size: rem(20);
			}

			@include min1050 {
				font-size: rem(14);
			}

			@include min660 {
				max-width: rem(240);
			}
		}

		&__count {
			padding: rem(2) rem(13);
			align-self: flex-start;
			display: flex;
			align-items: center;

			font-size: rem(17);
			line-height: 156%;
			border-radius: rem(60);
			border: rem(0.5) solid black;

			@include min1050 {
				padding: rem(2) rem(7);
				font-size: rem(9);
			}
		}

		&__price {
			display: flex;
			flex-direction: column;

			b {
				font-size: rem(24);
				line-height: 140%;

				@include min1050 {
					font-size: rem(12);
				}
			}

			span {
				color: var(--c-black-50);
				font-weight: 500;
				line-height: 140%;
				text-decoration: line-through;

				@include min1050 {
					font-size: rem(8);
				}
			}
		}

		.button_black {
			align-self: flex-start;
			margin-top: rem(44);

			padding: 0 rem(50);
			height: rem(50);
			background-color: black;
			border: rem(1) solid black;
			color: white;
			font-weight: 500;

			display: flex;
			align-items: center;
			justify-content: center;

			@include min1050 {
				margin-top: rem(28);
				padding: 0 rem(13);
				height: rem(34);
				font-size: rem(12);
			}

			&:hover {
				background-color: transparent;
				border: rem(1) solid black;

				color: black;
			}
		}
	}
}

