.catalog {

	margin-bottom: rem(122);

	.container {
		display: flex;
		flex-direction: column;
	}

	.title {
		font-size: rem(36);
		font-weight: 600;
		color: var(--c-gray-title);
		margin-bottom: rem(65);

		@include min1050 {
			font-size: rem(18);
			margin-bottom: rem(56);
		}
	}


	&__items {
		display: flex;
		flex-wrap: wrap;

		.block {
			margin-right: rem(146);

			@include min1050 {
				margin-bottom: rem(60);

				margin-right: rem(80);
			}
		}
	}

	&__item {
		display: flex;
		flex-direction: column;
		margin-bottom: rem(32);

		// svg {
		// 	margin-bottom: rem(8);

		// 	height: rem(28);
		// }

		.icon {
			margin-bottom: rem(8);
			height: rem(28);
			display: flex;
			align-items: flex-end;
		}

		.link {
			font-size: rem(20);
			font-weight: 600;
			color: #101010;
			margin-bottom: rem(32);

			@include min1050 {
				font-size: rem(16);
			}
		}

		> ul {
			> li {
				margin-bottom: rem(18);

				> a {
					font-size: rem(16);
					color: rgba(62,62,62,.7);

					&:hover {
						color: var(--c-gray-title);
					}
				}

				ul {
					margin-top: rem(7);
					li {
						list-style: disc;
						margin-left: rem(20);
						color: rgba(62, 62, 62, .8);

						margin-bottom: rem(7);

						&:last-child {
							margin-bottom: 0;
						}

						a {
							font-size: rem(14);
							color: rgba(62, 62, 62, .8);

							&:hover {
								color: var(--c-gray-title);
							}
						}
					}
				}
			}
		}
	}

	&__filter__smart {
		height: rem(54);
		width: 100%;
		border-radius: rem(5);
		background-color: white;
		padding: 0 rem(15);
		margin-bottom: rem(30);

		display: flex;
		align-items: center;

		span {
			color: black;
		}
	}

	&__filter__smart.hide {
		span {
			display: none;
		}

		&::before {
			content: " ";
		}
	}

	&__filter {
		// height: rem(58);
		background-color: white;
		border-radius: rem(5);
		// padding: rem(0) rem(42) 0 rem(20);

		display: flex;
		align-items: center;
		justify-content: space-between;

		margin-bottom: rem(20);

		@include min1200 {
			position: relative;
			display: none;

			flex-direction: column;
			align-items: flex-start;
		}

		&_item {
			// position: relative;
			display: flex;
			align-items: center;

			@include max1200 {
				position: relative;
			}
			

			&:first-child {
				.left {
					color: #FFB571;
				}
				.right {
					color: black;
				}
			}

			&:last-child {
				.catalog__filter_item__drop {
					left: auto;
					right: 0;
				}
			}

			> a, > button {
				display: flex;
				align-items: center;
				height: rem(58);
				padding: 0 rem(20);
				// overflow: hidden;
			}

			.left {
				color: rgba(0,0,0, .56);
				margin-right: rem(12);
			}

			.right {
				
				display: flex;
				align-items: center;
				

				.text {
					color: rgba(92, 92, 92, .56);
					max-width: rem(120);
					overflow: hidden;
					white-space: nowrap;
					text-overflow: ellipsis;

					@include min1440 {
						max-width: rem(64);
					}

					@include min1200 {
						max-width: 100%;
					}
				}

				

				svg {
					margin-left: rem(7);
					// margin-bottom: rem(1);

					use {
						stroke: #5C5C5C;
					}
				}
			}

			&__drop {
				position: absolute;
				top: 100%;
				left: 0;
				z-index: 5;
				width: rem(220);
				padding: rem(24);
				border-radius: rem(5);
				background-color: white;
				box-shadow: 0px 4px 5px rgba(0, 0, 0, 0.2),
				0px 4px 50px rgba(0, 0, 0, 0.4);

				margin-top: rem(14);

				display: none;

				@include min1200 {
					top: 0;
					width: 100%;
					height: 100%;
					box-shadow: none;
					margin-top: 0;
				}

				ul {
					display: flex;
					flex-direction: column;

					@include min1200 {
						// flex-wrap: wrap;
						height: 100%;
					}

					li {
						margin-bottom: rem(26);

						&:last-child {
							margin-bottom: 0;
						}

						a, button {
							font-weight: 500;
							color: #5C5C5C;
							text-align: left;

							@include min540 {
								white-space: nowrap;
							}
						}
					}

					li.active {
						a,
						button {
							color: #212121;
						}
					}
				}
			}

			&__drop.col2 {
				width: rem(405);

				// display: flex;
				justify-content: space-between;

				@include min1200 {
					justify-content: flex-start;
					width: 100%;

					ul {
						margin-right: rem(40);
					}
				}

				@include min540 {
					ul {
						margin-right: 0;
						margin-bottom: rem(28);
					}
				}
			}
		}

		&_item.active {
			
			@include max1200 {
				>a,
				>button {
					box-shadow: 0px 4px 5px rgba(0, 0, 0, 0.2),
						0px 4px 50px rgba(0, 0, 0, 0.4);
					border-radius: rem(5);

					.right {
						svg {
							transform: rotate(180deg);
						}
					}
				}
			}
			

			.catalog__filter_item__drop {
				display: block;
			}

			.catalog__filter_item__drop.col2 {
				display: flex;
				
				@include min540 {
					flex-direction: column;

					overflow: auto;
				}
			}
		}
	}

	&__filter.active {
		@include min1200 {
			display: flex;
		}
	}


	&__count {
		margin-bottom: rem(46);

		display: flex;
		align-items: center;

		span {
			margin-right: rem(20);
			color: var(--c-black-70);
			font-weight: 500;
		}

		&_btn {
			width: rem(30);
			height: rem(30);
			display: flex;
			align-items: center;
			justify-content: center;
			border-radius: 50%;

			color: var(--c-black-60);

			margin-right: rem(12);

			&:hover {
				background-color: black;
				color: white;
			}

			&:last-child {
				margin-right: 0;
			}
		}

		&_btn.active {
			background-color: black;
			color: white;
		}
	}

	&__product {
		display: flex;
		flex-wrap: wrap;

		@include min1050 {
			margin-bottom: rem(128);
		}

		.product_item {
			width: calc((100% - 44px) / 3);

			margin-right: rem(22);
			margin-bottom: rem(32);

			@include min1050 {
				width: calc((100% - 22px) / 2);

				&:nth-child(2n) {
					margin-right: 0;
				}
			}

			@include min540 {
				width: 100%;

				margin-right: 0;
				margin-bottom: rem(48);
			}

			&:nth-child(3n) {
				

				@include max1050 {
					margin-right: 0;
				}
			}

			&__image {
				height: rem(280);
			}

			&__subtitle {
				font-size: rem(16);
			}

			&__list {
				ul {
					li {
						font-size: rem(14);
					}
				}
			}
		}
	}

}