.article_product {

	margin-bottom: rem(112);

	&__top {
		display: flex;
		flex-wrap: wrap;
		margin-bottom: rem(120);
	}

	&__left {
		flex: 0 0 65%;
		max-width: 65%;
		
		margin-right: rem(68);

		display: flex;
		flex-direction: column;
		

		@include min1440 {
			margin-right: rem(32);
		}

		@include min1340 {
			flex: 0 0 58%;
			max-width: 58%;
		}

		@include min1050 {
			flex: 0 0 100%;
			max-width: 100%;
			margin-right: 0;
		}

		.article_product_slider_main {
			width: 100%;
			height: rem(568);
			margin-bottom: rem(12);

			border-radius: rem(5);
			overflow: hidden;

			@include min1050 {
				height: rem(430);
			}

			@include min540 {
				height: rem(212);
			}

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}

			.swiper-nav {
				position: absolute;
				top: 50%;
				margin-top: rem(-20);
				z-index: 2;
				width: 100%;
				display: flex;
				justify-content: space-between;

				button {
					width: rem(45);
					height: rem(60);

					display: flex;
					align-items: center;
					justify-content: center;
					background-color: rgba(255, 255, 255, .34);

					

					&:hover {
						background-color: rgba(255, 255, 255, .65);
					}
				}
			}
		}

		.article_product_slider_thumbs {
			width: 100%;

			.swiper-slide {
				width: rem(148);
				height: rem(100);
				border-radius: rem(5);
				overflow: hidden;

				cursor: pointer;

				opacity: .6;

				transition: var(--transition);

				@include min540 {
					width: rem(100);
					height: rem(70);
				}

				&:hover {
					opacity: 1;
				}

				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}
			
			.swiper-slide-thumb-active {
				opacity: 1;
			}
		}
	}


	&__right {
		flex: 0 0 29.5%;
		max-width: 29.5%;

		display: flex;
		flex-direction: column;

		@include min1440 {
			flex: 0 0 32.5%;
			max-width: 32.5%;
		}

		@include min1340 {
			flex: 0 0 37.5%;
			max-width: 37.5%;
		}

		@include min1050 {
			flex: 0 0 100%;
			max-width: 100%;
			margin-top: rem(50);
		}

		.title {
			margin-bottom: rem(15);
			font-size: rem(36);
			font-weight: bold;
			line-height: 112%;

			@include min1440 {
				font-size: rem(30);
			}

			@include min1050 {
				font-size: rem(24);
			}

			@include min768 {
				font-size: rem(20);
			}
		}

		.list {
			display: flex;
			flex-direction: column;
			margin-bottom: rem(60);

			li {
				display: flex;
				padding: rem(15) 0;
				border-bottom: rem(0.5) solid var(--c-black-40);
				
				b {
					font-weight: 500;
					line-height: 156%;
					text-transform: uppercase;

					flex: 0 0 45%;
					max-width: 45%;

					@include min540 {
						font-size: rem(14);
						flex: 0 0 50%;
						max-width: 50%;
					}
				}

				span, p {
					line-height: 156%;
					color: #6F6F6F;

					@include min540 {
						font-size: rem(12);
					}
				}

				.flex {
					span {
						position: relative;
						margin-right: rem(6);
						text-decoration: underline;

						&::after {
							content: ",";
						}

						&:last-child {
							&::after {
								content: "";
							}
						}
					}
				}
			}
		}

		.price {
			font-size: rem(24);
			line-height: 156%;
			margin-bottom: rem(15);
		}

		.buttons {
			display: flex;
			align-items: center;

			.button_black,
			.button_transparent {
				padding: rem(14) rem(28);

				span {
					white-space: nowrap;
				}
			}
		}
	}


	&__carts {
		display: flex;
		flex-wrap: wrap;
		margin-bottom: rem(100);

		.swiper {
			padding-right: rem(66);
			margin-right: rem(-66);

			@include min540 {
				padding-right: rem(20);
				margin-right: rem(-20);
			}
		}

		.swiper-control {
			display: none;
			// display: flex;
			justify-content: space-between;
			padding: rem(32) 0;
			width: 100%;

			@include min1200 {
				display: flex;
				justify-content: flex-end;
				padding: rem(16) 0;
			}

			.swiper-prev,
			.swiper-next {
				width: rem(50);

				svg {
					use {
						stroke: #3E3E3E;
					}
				}

				@include min1200 {
					width: rem(38);
					height: rem(38);
					border-radius: 50%;

					display: flex;
					align-items: center;
					justify-content: center;

					border: rem(1) solid #3E3E3E;

					&:hover,
					&:active {
						background-color: black;
						border-color: black;

						svg {
							use {
								stroke: white;
							}
						}
					}

					svg {
						width: rem(16);
					}
				}
			}

			.swiper-next {
				@include min1200 {
					margin-left: rem(10);
				}
			}
		}

		.swiper-slide {
			background-color: white;
			padding: rem(12);
			border-radius: rem(5);

			display: flex;
			flex-direction: column;

			// flex: 0 0 calc((100% - 96px) / 5);
			// max-width: calc((100% - 96px) / 5);
			// margin-right: rem(24);

			&:last-child {
				margin-right: 0;
			}

			p {
				margin-top: rem(20);
				font-weight: 500;
				line-height: 130%;
				color: #888888;
			}
		}
	}

	&__tabs {
		display: flex;
		flex-direction: column;

		padding-bottom: rem(44);

		border-bottom: rem(0.5) solid var(--c-black-40);
		margin-bottom: rem(100);

		

		.list_tabs {
			display: flex;
			border-bottom: rem(3) solid rgba(0,0,0,.25);
			margin-bottom: rem(28);

			@include min768 {
				margin-right: rem(20);
				margin-right: rem(-20);
			}

			@include min540 {
				overflow-x: auto;
				overflow-y: hidden;
			}

			.list_tab {
				position: relative;
				padding: rem(18) rem(14);
				font-weight: 500;
				font-size: rem(18);
				color: var(--c-black-50);

				margin-right: rem(56);

				@include min768 {
					margin-right: rem(44);
					font-size: rem(14);
					white-space: nowrap;
				}

				&:last-child {
					margin-right: 0;
				}

				&:first-child {
					padding-left: 0;
				}

				&::before {
					content: "";
					position: absolute;
					left: 0;
					bottom: rem(-2.6);
					height: rem(2.6);
					background-color: #C87224;
					width: 100%;
					transition: var(--transition);
					opacity: 0;

					@include min768 {
						z-index: 2;
						bottom: rem(-1);
					}
				}

				&:hover {
					color: black;
					&::before {
						opacity: 1;
					}
				}
			}
			.list_tab.active {
				color: black;

				&::before {
					opacity: 1;
				}
			}
		}

		.box_tabs {
			margin-top: rem(28);

			.box_tab {
				display: none;

				p {
					line-height: 156%;
					color: #2B2B2B;
				}
			}
			.box_tab.active {
				display: block;
			}
		}
	}

	&__related {
		display: flex;
		flex-direction: column;
		margin-bottom: rem(130);

		.title_24 {
			margin-bottom: rem(42);
			font-weight: bold;
			color: black;
		}

		.swiper {
			// width: 100%;
			min-width: 0;
			padding: 0;
			margin: 0;

			@include max1050 {
				padding: rem(80) rem(80);
				margin: rem(-80) rem(-80);
			}

			.product_item__image {
				height: rem(280);
			}

			.swiper-wrapper {
				margin-bottom: rem(20);
			}

			.swiper-nav {
				display: flex;
				align-items: center;
				justify-content: center;

				@include min1050 {
					justify-content: flex-end;
				}

				.swiper-page {
					@include min1050 {
						display: none;
					}
				}

				.swiper-prev,
				.swiper-next {
					@include min1050 {
						width: rem(38);
						height: rem(38);
						border-radius: 50%;

						display: flex;
						align-items: center;
						justify-content: center;

						border: rem(1) solid #3E3E3E;

						&:hover,
						&:active {
							background-color: black;
							border-color: black;

							svg {
								use {
									stroke: white;
								}
							}
						}

						svg {
							width: rem(16);
						}
					}

					svg {
						use {
							stroke: #3E3E3E;
						}
					}
				}

				.swiper-next {
					@include min1050 {
						margin-left: rem(10);
					}
				}
			}

			.swiper-slide {
				opacity: 0;
				transition: var(--transition);
			}

			.swiper-slide-prev,
			.swiper-slide-next,
			.swiper-slide-active {
				opacity: 1;
			}
		}
	}
}